@import url(https://fonts.googleapis.com/css?family=Sniglet);
@import url(https://fontlibrary.org/face/couture);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #d1cfcf; }

.formulaScreen {
  min-height: 40px;
  color: #656668;
  text-align: right;
  vertical-align: text-top;
  line-height: 30px;
  width: 300px;
  height: 20px; }

.outputScreen {
  font-size: 25px;
  color: black;
  text-align: right;
  line-height: 35px;
  font-weight: bold; }

button {
  position: relative;
  height: 65px;
  width: 80px;
  color: black;
  outline: 1.5px solid black;
  border: none;
  background: whitesmoke;
  font-family: "couture", monospace;
  font-size: 25px;
  cursor: default; }
  button:hover {
    color: #5a5959;
    outline: 1px solid grey;
    z-index: 3; }

.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) perspective(600px) rotateY(20deg) rotateX(10deg);
          transform: translate(-50%, -50%) perspective(600px) rotateY(20deg) rotateX(10deg);
  background: #111111;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 260px;
  height: 350px;
  border-radius: 10px; }
  .wrapper .shape {
    background: #4e4e4f;
    border-radius: 10px;
    width: 260px;
    height: 350px;
    -webkit-transform: translateZ(50px) scale(0.85);
            transform: translateZ(50px) scale(0.85);
    opacity: 0.8; }
  .wrapper .shape2 {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 320px;
    height: 400px;
    -webkit-transform: translateZ(150px) translateX(-27px) translateY(-26px) scale(0.55);
            transform: translateZ(150px) translateX(-27px) translateY(-26px) scale(0.55);
    border: 4px solid black;
    background: white;
    opacity: 1;
    border-radius: 8px; }
  .wrapper p {
    text-align: center; }

